a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}

h3 {
  color: #e61b20;
}

.recruitment-info {
  padding: 60px 0;
}

.vertical-tab {
  width: 100%;
  display: table;
}
.vertical-tab .nav-tabs {
  display: table-cell;
  width: 28%;
  min-width: 28%;
  border: none;
  position: relative;
}
.vertical-tab .nav-tabs li {
  float: none;
  vertical-align: top;
}
.vertical-tab .nav-tabs li a {
  color: #303030;
  background-color: #e7e7e7;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  padding: 12px;
  margin: 0;
  border-radius: 0;
  border: none;
  display: block;
  overflow: hidden;
  position: relative;
  z-index: 1;
  transition: all 0.3s ease 0s;
}
.vertical-tab .nav-tabs li a:hover,
.vertical-tab .nav-tabs li.active a,
.vertical-tab .nav-tabs li.active a:hover {
  color: #fff;
  background-color: transparent;
  border: none;
}
.vertical-tab .nav-tabs li a:before,
.vertical-tab .nav-tabs li a:after {
  content: "";
  height: 100%;
  width: 100%;
  background-color: #e61b20;
  position: absolute;
  left: -100%;
  top: 0;
  z-index: -1;
  transition: all 0.3s ease 0s;
}
.vertical-tab .nav-tabs li a:after {
  background-color: #303030;
  width: 5px;
  transform: scaleY(0);
  left: 0;
  transition: all 0.3s ease 0.1s;
}
.vertical-tab .nav-tabs li.active a:before,
.vertical-tab .nav-tabs li a:hover:before {
  left: 10px;
}
.vertical-tab .nav-tabs li.active a:after,
.vertical-tab .nav-tabs li a:hover:after {
  transform: scale(1);
}
.vertical-tab .tab-content {
  color: #fff;
  background: #303030;
  font-size: 15px;
  text-align: justify;
  line-height: 23px;
  vertical-align: top;
  padding: 15px 18px 0;
  display: table-cell;
}
.vertical-tab .tab-content h3 {
  font-size: 24px;
  margin: 0 0 5px 0;
}
@media only screen and (max-width: 479px) {
  .vertical-tab .nav-tabs {
    display: block;
    width: 100%;
    border-right: none;
  }
  .vertical-tab .nav-tabs li a {
    padding: 10px;
    margin: 0 0 10px;
  }
  .vertical-tab .tab-content {
    display: block;
    padding: 20px 15px 5px;
    border-radius: 0 0 10px 10px;
  }
  .vertical-tab .tab-content h3 {
    font-size: 18px;
  }
}
